@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');

@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap');

body {
  background:rgb(246, 246, 246);
  font: 400 18px/22px 'Roboto', sans-serif; 
}
h2 {
  font: 400 24px/28px 'Roboto', sans-serif; 
}
.App,
.enter {
  text-align: center;
  max-width:  450px;
  margin: 5px auto; 
  padding: 0 5px;
  padding-bottom: 20px;
  font: 400 18px/22px 'Roboto', sans-serif;
}
.inlineInput {
  text-align: left;
  padding: 0;
  margin: 0 0 30px;
  display: block; 
}
a {
  display: block;
  border-radius: 5px;
  border: 1px solid #ccc;
  margin: 30px auto;
  font: 400 22px/28px 'Motnserrat', sans-serif;  
  background: #fff;
  padding: 10px 0;
  height: auto !important;
}
a:hover {
 border: 1px solid #1890ff;
}
.App-header {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  border-radius: 5px; 
  z-index: 100;
  background-color: #fff;
  min-height: 10vh; 
  align-items: center; 
  color: white; 
  background-image: url(https://lh4.googleusercontent.com/QuWIIu1QGx7xszEwcZHl9EPUhMkBjB4CrLzI0jHw7v3d20mRlxNX9WyiljOj2Kv57m-19X3g0cQYXZk75EgU6nzfi_Tz52mEjbQGhGXv5L1s8DX-av-YimpJGUtnQR1fsg=w3196);
  background-size: cover;
  background-position: center;
}
.add {
  text-align: center;
  margin: 0 auto;
  width: fit-content;
  padding: 10px ;
  font: 600 18px/20px 'Roboto', sans-serif;
  display:block;
  cursor: pointer
}
.add:hover {
  background: rgb(175, 196, 197);
}
.addPhoto {
  text-align: left;
  margin: 0 0 20px;
  display:block;
  cursor: pointer
}
.photo {
  text-align: left;  
  margin: 0 0 20px;
  padding: 10px 0;
}
.photo label {
  margin-right: 10px;
  cursor: pointer;
}
.ant-input {
  border-radius: 5px;
  height: 50px;
  font-size: 20px;
}
.ant-card-head-title,.ant-form-item-label {
  white-space: normal;
  font:600 20px/20px 'Montserrat' , sans-serif;
}
.ant-card {
  border-radius: 5px;
  margin-bottom: 10px; 
}
.ant-card {
  padding-bottom: 0;
  border: 1px solid #ccc;
}
.ant-card-body {
  padding-bottom: 0;
  
}
.addAnketa {
  text-align: left;
  display: block;
  margin-top: 15px;
}
.ant-upload-list:before {
  opacity:0;
}
.ant-tooltip-open::before {
  display: none;
}
.ant-radio-button-wrapper {
  
  display: block;
}
.ant-radio-group {
  width: 100%;
  margin-bottom: 20px;
}
